
import { AjaxHttp, AjaxSilentHttp } from '../../../js/ajaxHttp'
import ListCell from '../../crm/components/ListCell.vue'
import { getYMDFormateTime, getYMFormateTime, getTimestamp, isArrayType, isNullValue } from "../../../js/utility.js"
import { mapState } from 'vuex'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Datetime from 'vux/src/components/datetime/index.vue'


export default {
name: 'checkstatistics',
components: {
ListCell,
Tabbar,
TabbarItem,
Datetime
},
computed: {
...mapState([
'userInfo',
'checkStatisInfo',
'detail',
]),
endDate() {
return getYMDFormateTime(getTimestamp(new Date()));
}
},
data() {
return {
selectDateF: '',
contentArray: [],
datePickerVisibility: false,
setting: false,
}
},
beforeRouteLeave(to, from, next) {

if (to.name === 'office') { //到办公页面清空数据
this.$store.commit('updateDetail', '');
this.$store.commit('updateCheckStatisInfo', {}); 
this.$store.commit('updateCheckSetInfo', {});
} else {
this.$store.commit('updateCheckStatisInfo', {
type: 'checkstatistics',
selectDateF: this.selectDateF,
contentArray: this.contentArray,
});
}
next();
},
mounted() {
this.setting = this.detail.setting;

if (this.checkStatisInfo.type === 'checkstatistics') {
this.$nextTick(() => {
this.selectDateF = this.checkStatisInfo.selectDateF;
});
this.contentArray = this.checkStatisInfo.contentArray;
this.getSilentDetail(this.getYMFormateTimestamp(this.checkStatisInfo.selectDateF));
} else {
this.$nextTick(() => {
this.selectDateF = getYMFormateTime(getTimestamp(new Date()));
});
this.getdetail(getTimestamp(new Date()));
}

},
methods: {
enterUser(role_id) { //进入个人详情
this.$router.push({
name: 'user',
params: {
id: role_id
}
})
},
clickShow(item) {
if (item.content.length > 0) {
item.show = !item.show;
}
},
getdetail(time) {
AjaxHttp('POST', 'm=kaoqin&a=analytics', { time: time }, this, function(res, wself, success) {
if (success) {
wself.contentArray = [];
wself.handleInfo(res);
}
});
},
getSilentDetail(time) {
AjaxSilentHttp('POST', 'm=kaoqin&a=analytics', { time: time }, this, function(res, wself, success) {
if (success) {
if (isArrayType(res.list.num_a_list)) {
var item = wself.contentArray[0];
item.content = res.list.num_a_list;
}
if (isArrayType(res.list.num_b_list)) {
var item = wself.contentArray[1];
item.content = res.list.num_b_list;
}
if (isArrayType(res.list.num_c_list)) {
var item = wself.contentArray[2];
item.content = res.list.num_c_list;
}
if (isArrayType(res.list.num_d_list)) {
var item = wself.contentArray[3];
item.content = res.list.num_d_list;
}
if (isArrayType(res.list.num_e_list)) {
var item = wself.contentArray[4];
item.content = res.list.num_e_list;
}
if (isArrayType(res.list.num_f_list)) {
var item = wself.contentArray[5];
item.content = res.list.num_f_list;
}
}
});
},
handleInfo(res) {
if (isArrayType(res.list.num_a_list)) {
this.contentArray.push({
'title': '出勤',
'content': res.list.num_a_list,
'show': false
})
}
if (isArrayType(res.list.num_b_list)) {
this.contentArray.push({
'title': '迟到',
'content': res.list.num_b_list,
'show': false
})
}
if (isArrayType(res.list.num_c_list)) {
this.contentArray.push({
'title': '早退',
'content': res.list.num_c_list,
'show': false
})
}
if (isArrayType(res.list.num_d_list)) {
this.contentArray.push({
'title': '缺卡',
'content': res.list.num_d_list,
'show': false
})
}
if (isArrayType(res.list.num_e_list)) {
this.contentArray.push({
'title': '旷工',
'content': res.list.num_e_list,
'show': false
})
}
if (isArrayType(res.list.num_f_list)) {
this.contentArray.push({
'title': '休息',
'content': res.list.num_f_list,
'show': false
})
}
},
getYMFormateTimestamp(time) {
time = time + '-01 00:00:00';
var t = time.split(/[- :]/);
var d = new Date(t[0], t[1] - 1, t[2], t[3], t[4], t[5]);
var timestamp = new Date(d).getTime().toString();
timestamp = timestamp.substr(0, 10);
return timestamp;
},
getYMDWeekHMFormateTime(time) { //获取格式化时间
if (time === '' || time === '0' || isNullValue(time)) {
return '';
}
var times = 0;
if (time.length === 13) {
times = parseInt(time);
} else {
times = parseInt(time) * 1000;
}
var date = new Date(times); 
var Y = date.getFullYear() + '-';
var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
var D = (date.getDate() < 10 ? '0' + (date.getDate()) : date.getDate());
var week = this.getWeekName(date.getUTCDay());
var h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
var m = (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes());

return Y + M + D + week + h + m;
},
getYMDWeekFormateTime(time) { //获取格式化时间
if (time === '' || time === '0' || isNullValue(time)) {
return '';
}
var times = 0;
if (time.length === 13) {
times = parseInt(time);
} else {
times = parseInt(time) * 1000;
}
var date = new Date(times); 
var Y = date.getFullYear() + '-';
var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
var D = (date.getDate() < 10 ? '0' + (date.getDate()) : date.getDate());
var week = this.getWeekName(date.getUTCDay());
return Y + M + D + week;
},
getWeekName(week) {
switch (week) {
case 0:
return '（周日）';
break;
case 1:
return '（周一）';
break;
case 2:
return '（周二）';
break;
case 3:
return '（周三）';
break;
case 4:
return '（周四）';
break;
case 5:
return '（周五）';
break;
case 6:
return '（周六）';
break;
default:
break;
}

},
datePickerChange(value) {
if (value && this.selectDateF !== value) {
this.contentArray = [];
this.selectDateF = value;
this.getdetail(this.getYMFormateTimestamp(this.selectDateF));
}
},
getYMFormateTimestamp(time) {
time = time + '-01 00:00:00';
var t = time.split(/[- :]/);
var d = new Date(t[0], t[1] - 1, t[2], t[3], t[4], t[5]);
var timestamp = new Date(d).getTime().toString();
timestamp = timestamp.substr(0, 10);
return timestamp;
},
onIndexChange(newIndex, oldIndex) {
if (!isNullValue(newIndex) && !isNullValue(oldIndex)) {
if (newIndex === 0) {
this.$router.replace({ name: 'officework' })
} else if (newIndex === 2) {
this.$router.replace({ name: 'checkset' })
}
}

},
enterCheckMonth(time) {
this.$router.push({
name: 'checkmonth',
params: {
time: time
}
});
}
}
}




